
.App {
  text-align: center;
  background-color: #e5e8ec;
}

.App-body {
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color:  #1b2345;
  font-family: Poppins;
  font-weight: 700;
}

.card { 
  color: black;
  width: 20rem;
  margin: 1rem;
}

.card-text{
  font-size: 4mm;
}

#card-title{
  font-weight: 700;
}

#card-body{
  font-family: sofia-pro;
  font-weight: 300;
}

body {
  
  font-family: "Rubik", sans-serif;
}

#product {
  padding-bottom: 20vh;
}

/* Navbar */
.navbar{
  background-color: #776cbe;
  border-radius: 0;
}

#brand {
  color: #e5e8ec;
  font-size: 35px;
  font-weight: 700;
  font-family: Poppins;
}

.rounded-pill {
  margin-left: 3mm;
}

/* Sidebar */
.shadow-md {
  box-shadow: 0px 5px 10px rgba(90, 116, 148, 0.3);
}
.sidebar{
  width: 280px;
  min-height: 100vh;
  box-shadow: 0px 4px 8px rgb(0 0 0 / 16%);
  background-color: #fff;
  position: fixed;
  top: 0;
  right: -100%;
  z-index: 1;
  transition: 0.5s;
}
.sidebar.active{
  right: 0;
}
.sd-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 20px 0px 20px;
}
.sidebar-overlay{
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(0,0,0,0.4); */
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}
.sidebar-overlay.active{
  opacity: 1;
  visibility: visible;
}
.sd-body{
  max-height: calc(100vh - 67px);
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* checkout items */
#cName {
  font-size: 15px;
  padding-top: 10px;
}

#cImg {
  max-height: 50mm;
}

#remove {
  position: absolute;
  right:    0;
  bottom:   0;
  width: 100%;
  height: 60px;
  border-radius: 0mm;
  background-color: #776cbe;
  padding-top: 5mm;
}

#alert {
  background-color: #776cbe;
  border-color: #776cbe;
  color: white;
}